import React, {useState, useEffect} from 'react'
import PartyCard from '../../components/Party/PartyCard';
import { withAuth } from '../../context/auth.context';
import PartyService from '../../services/parties.service'
import "./PartyList.css"
import { Link, withRouter } from "react-router-dom";
import ReactMapGL, { Marker, Popup } from "react-map-gl"
import Navbar from "../../components/NavBar/NavBar"
import { makeStyles } from "@material-ui/core/styles";

function PartyList(props) {
  

  const [parties, setParties] = useState([]);
  const [displayedParties, setDisplayedParties] = useState(parties);
  const [marker, setSelectedMarker] = useState(null)
  
  const[viewport, setViewport] = useState({
    latitude: 41.390205,
    longitude: 2.154007,
        width: "48vw",
        height: "100vh",
        zoom: 12
  })
  
  const partyService = new PartyService();

  console.log()
  const refreshState = () => {
    partyService
      .get()
      .then((response) => {
       
        setParties(response.data);
      })
      .catch((err) => console.error(err));
  }

  const findByCity = (query) => {
  
    return setDisplayedParties(
        
        displayedParties.filter((party) => {
           return party.city.toLowerCase().includes(query.toLowerCase());
        }
         
        )
      );
    
    
  }

  const findByDate = (date) => {
    return setParties(
      parties.filter(party => {
        return party.date.substring(0,10) === String(date).substring(0,10)
      })
    )
  }
  
    
    

  
  useEffect(() => {
     console.log("Aqui estan les props", props)
    refreshState();
   }, [])
   
  
  

  const displayParties = () => {
    
  
    return parties.map((party) => {
      
      return (
        <div>
          <Link
            style={{ textDecoration: "none" }}
            key={party.id}
            to={`/party-details/${party.id}`}
          >
            <PartyCard
              
              {...party}
              
            />
          </Link>
        </div>
      );
    });
  }

  

  
    return (
      <div>
        <div>
          <Navbar
            className="navbar"
            findByDate={(time) => findByDate(time)}
            findByCity={(query) => findByCity(query)}
            isHomePage={false}
          />
        </div>
        <h1 className="title">Parties in Barcelona</h1>

        <div className="parties">{displayParties()}</div>
        <ReactMapGL
          className="map"
          {...viewport}
          mapboxApiAccessToken={process.env.REACT_APP_MAPBOX_TOKEN}
          onViewportChange={(viewport) => {
            setViewport(viewport);
          }}
          mapStyle="mapbox://styles/mapbox/streets-v11" //importar de mapbox styles
        >
          {parties.map((party) => {
            {console.log(party.longitude)
          }
            <Marker latitude={party.latitude} longitude={party.longitude}>
              <button
                className="marker-btn"
                onClick={(e) => {
                  e.preventDefault();
                  setSelectedMarker(marker);
                }}
              >
                <img src="https://picsum.photos/200/300" alt="" />
              </button>
            </Marker>;
          })}
          {marker ? (
            <Popup
              latitude={marker.latitude}
              longitude={marker.longitude}
              onClose={() => {
                setSelectedMarker(null);
              }}
            >
              <div>
                {" "}
                <h2>{marker.name}</h2>
                <p>{marker.description}</p>
              </div>
            </Popup>
          ) : null}
        </ReactMapGL>
      </div>
    );
  }


export default withAuth(PartyList);